

// import { useEffect, useState } from 'react';
import { useEffect, useState } from 'react';
import AwesomeSlider from 'react-awesome-slider';
import withAutoplay from 'react-awesome-slider/dist/autoplay';
import 'react-awesome-slider/dist/styles.css';

import { Link } from "react-router-dom";
import { ScaleLoader } from 'react-spinners';

export default function Home() {

  const AutoplaySlider = withAutoplay(AwesomeSlider);
  const [products, setProducts] = useState(null);

  const getProducts = ()=>{
    return new Promise(async (resolve) =>{
      const response = await fetch('/products?_page=1&_limit=20')
      resolve(response.json());
    }).then((data)=>{
      setProducts(data)
    })
  }

  useEffect(()=>{
    getProducts();
  },[])

  return (
    <>
    {products === null ?
      (
        <div className='flex w-[100vw] h-[100vh] items-center justify-between'>
          <ScaleLoader color="#36d7b7" className='m-auto'/>
        </div>
      ) :
      (<main className=" m-auto flex min-h-screen max-w-7xl flex-col items-center justify-between">
        <div className="h-[50vw] lg:h-[35vw] w-[95%]  my-4 mx-3 rounded-lg">
          <AutoplaySlider
          play={true}
          cancelOnInteraction={false}
          interval={2000}
          bullets={false}
          organicArrows = {false}
          className="w-[100%] h-[100%] rounded-[10px] overflow-hidden"
          >
              <div data-src="/Banner_1.png" />
              <div data-src="/Banner_2.jpg" />
              <div data-src="/Banner_3.jpg" />
              <div data-src="/Banner_4.jpg" />
          </AutoplaySlider>
        </div>
        <div className="max-w-screen-xl mx-auto">
          <div className="flex flex-wrap flex-col">
            <Link
                className="m-auto text-xs md:text-sm lg:text-lg my-3 bg-[#08a3f2] text-white px-3 py-2 w-fit rounded-lg"
                to='/products'
                >
                See All products
            </Link>
            <h3 className="text-center text-2xl lg:4xl m-2">Our Top Products</h3>
            <ul className="flex flex-wrap flex-row gap-3 items-center justify-center">
              {products && products.map((product) => (
                  (product.deleted===false || product.deleted===undefined)
                  &&
                  <div className='w-[150px] lg:w-[220px] bg-white'>
                    <Link to={`/product-detail/${product.id}`} key={product.id} >
                      <div className='flex flex-col justify-between p-1 max-h-[340px]  lg:max-h-[440px]'>
                        <div className='flex justify-center h-[190px] w-[140px] lg:h-[280px] lg:w-[210px] bg-white'>
                          <img
                            src={product.thumbnail}
                            alt={product.title}
                            className="h-[100%] lg:max-w-[210px] max-w-[147px] "
                          />
                        </div>
                        <div className="flex justify-between w-[140px] lg:w-[210px]">
                          <div className=' w-[90px] lg:w-[165px]'>
                            <h3 className="text-md text-black font-medium truncate">
                                {product.title}
                            </h3>
                          </div>
                          <div className='flex flex-col justify-around w-[55px] pr-1 lg:w-[60px] text-end'>
                            <p className="text-md text-black font-medium truncate text-green-600">
                              $
                              {product.discountPrice}
                            </p>
                            <p className="text-sm block line-through font-medium text-gray-400">
                              ${product.price}
                            </p>
                          </div>
                        </div>
                      </div>
                    </Link>
                  </div>
              ))}
            </ul>
            <Link
              className="m-auto text-xs md:text-sm lg:text-lg my-3 bg-[#08a3f2] text-white px-3 py-2 w-fit rounded-lg"
              to='/products'
              >
              See All products
            </Link>
          </div>
        </div>
    </main>)}
    </>
  );
}