import React, { useState, Fragment, useEffect } from 'react';
import { useSelector, useDispatch } from 'react-redux';
import {
  empty,
  fetchBrandsAsync,
  fetchCategoriesAsync,
  pagingAsync,
  selectAllProducts,
  selectBrands,
  selectCategories,
  selectProductListStatus,
  selectTotalItems,

} from '../productSlice';
import { Dialog, Disclosure, Menu, Transition } from '@headlessui/react';
import { XMarkIcon } from '@heroicons/react/24/outline';
import { Link } from 'react-router-dom';
import {
  ChevronDownIcon,
  FunnelIcon,
  MinusIcon,
  PlusIcon,
  ShoppingCartIcon,
  StarIcon,
} from '@heroicons/react/20/solid';
import { ITEMS_PER_PAGE } from '../../../app/constants';
import InfiniteScroll from 'react-infinite-scroll-component';
import { selectItems } from '../../cart/cartSlice';
import { ScaleLoader } from 'react-spinners';

const sortOptions = [
  { name: 'Best Rating', sort: 'rating', order: 'desc', current: false },
  { name: 'Low to High', sort: 'discountPrice', order: 'asc', current: false },
  { name: 'Price: High to Low', sort: 'discountPrice', order: 'desc', current: false },
];


function classNames(...classes) {
  return classes.filter(Boolean).join(' ');
}

export default function ProductList() {

  const dispatch = useDispatch();
  const products = useSelector(selectAllProducts);
  const totalItems = useSelector(selectTotalItems);
  const brands = useSelector(selectBrands);
  const categories = useSelector(selectCategories);
  const status = useSelector(selectProductListStatus);


  const [filter, setFilter] = useState({});
  const [sort, setSort] = useState({});
  const [page, setPage] = useState(1);
  const [dependency, setDependency] = useState(true);
  const [mobileFiltersOpen, setMobileFiltersOpen] = useState(false);

  const filters = [
    {
      id: 'category',
      name: 'Category',
      options: categories,
    },
    {
      id: 'brand',
      name: 'Brands',
      options: brands,
    },
  ];
  const handleFilter = (e, section, option) => {
    const newFilter = { ...filter };
    if (e.target.checked) {
      if (newFilter[section.id]) {
        newFilter[section.id].push(option.value);
      } else {
        newFilter[section.id] = [option.value];
      }
    } else {
      const index = newFilter[section.id].findIndex(
        (el) => el === option.value
      );
      newFilter[section.id].splice(index, 1);
    }
    setFilter(newFilter);
    dispatch(empty());
    setPage(1);
    setDependency(!dependency);
  };

  const handleSort = (e, option) => {
    const sort = { _sort: option.sort, _order: option.order };
    setSort(sort);
    dispatch(empty());
    setPage(1);
    setDependency(!dependency);
  };

  const loadMoreProducts= async ()=>{
    setPage(page+1);
    setDependency(!dependency);
  }

  useEffect(()=>{
    const pagination = { _page: page, _limit: ITEMS_PER_PAGE }
    dispatch(pagingAsync({filter,sort,pagination}));
  },[dependency])

  useEffect(() => {
    dispatch(empty());
    dispatch(fetchBrandsAsync());
    dispatch(fetchCategoriesAsync());
  }, []);



  return (
    <>
    {products.length === 0 ?
      (
        <div className='flex w-[100vw] h-[100vh] items-center justify-between'>
          <ScaleLoader color="#36d7b7" className='m-auto'/>
        </div>
      ) :
      (<div className="bg-white">
        <div>
          <MobileFilter handleFilter={handleFilter} mobileFiltersOpen={mobileFiltersOpen} setMobileFiltersOpen={setMobileFiltersOpen} filters={filters}>
          </MobileFilter>

          <main className="max-w-full bg-white">

            <ProductsFlex handleSort={handleSort} setMobileFiltersOpen={setMobileFiltersOpen}></ProductsFlex>

            <section aria-labelledby="products-heading">
              <div className="flex gap-2 pl-2 pr-2 lg:pl-4 lg:pr-4">
                  <DesktopFilter handleFilter={handleFilter} filters={filters}></DesktopFilter>

                  <InfiniteScroll
                    dataLength={products.length}
                    next={loadMoreProducts}
                    hasMore={products.length!=totalItems}
                    // height='100vh'
                    className='flex flex-col items-center justify-center'
                    loader={<ScaleLoader color="#36d7b7" className='m-auto'/>}
                    >
                    <ProductGrid products={products}></ProductGrid>
                  </InfiniteScroll>
              </div>
            </section>
          </main>
        </div>
      </div>)
    }
    </>
  );
}

function MobileFilter({mobileFiltersOpen,setMobileFiltersOpen,handleFilter,filters})
{
    return(
      <Transition.Root show={mobileFiltersOpen} as={Fragment}>
      <Dialog
        as="div"
        className="relative z-40 lg:hidden"
        onClose={setMobileFiltersOpen}
      >
        <Transition.Child
          as={Fragment}
          enter="transition-opacity ease-linear duration-300"
          enterFrom="opacity-0"
          enterTo="opacity-100"
          leave="transition-opacity ease-linear duration-300"
          leaveFrom="opacity-100"
          leaveTo="opacity-0"
        >
          <div className="fixed inset-0 bg-black bg-opacity-25" />
        </Transition.Child>

        <div className="fixed inset-0 z-40 flex">
          <Transition.Child
            as={Fragment}
            enter="transition ease-in-out duration-300 transform"
            enterFrom="translate-x-full"
            enterTo="translate-x-0"
            leave="transition ease-in-out duration-300 transform"
            leaveFrom="translate-x-0"
            leaveTo="translate-x-full"
          >
            <Dialog.Panel className="relative ml-auto flex h-full w-56  flex-col overflow-y-auto bg-white py-4 pb-12 shadow-xl">
              <div className="flex items-center justify-between px-4">
                <h2 className="text-lg font-medium text-gray-900">
                  Filters
                </h2>
                <button
                  type="button"
                  className="-mr-2 flex h-10 w-10 items-center justify-center rounded-md bg-white p-2 text-gray-400"
                  onClick={() => setMobileFiltersOpen(false)}
                >
                  <span className="sr-only">Close menu</span>
                  <XMarkIcon className="h-6 w-6" aria-hidden="true" />
                </button>
              </div>

              {/* Filters */}
              <form className="mt-4 border-t border-gray-200">
                {filters.map((section) => (
                  <Disclosure
                    as="div"
                    key={section.id}
                    className="border-t border-gray-200 px-4 py-6"
                  >
                    {({ open }) => (
                      <>
                        <h3 className="-mx-2 -my-3 flow-root">
                          <Disclosure.Button className="flex w-full items-center justify-between bg-white px-2 py-3 text-gray-400 hover:text-gray-500">
                            <span className="font-medium text-gray-900">
                              {section.name}
                            </span>
                            <span className="ml-6 flex items-center">
                              {open ? (
                                <MinusIcon
                                  className="h-5 w-5"
                                  aria-hidden="true"
                                />
                              ) : (
                                <PlusIcon
                                  className="h-5 w-5"
                                  aria-hidden="true"
                                />
                              )}
                            </span>
                          </Disclosure.Button>
                        </h3>
                        <Disclosure.Panel className="pt-6">
                          <div className="space-y-6">
                            {section.options.map((option, optionIdx) => (
                              <div
                                key={option.value}
                                className="flex items-center"
                              >
                                <input
                                  id={`filter-mobile-${section.id}-${optionIdx}`}
                                  name={`${section.id}[]`}
                                  defaultValue={option.value}
                                  type="checkbox"
                                  defaultChecked={option.checked}
                                  onChange={(e) =>handleFilter(e, section, option)}
                                  className="h-4 w-4 rounded border-gray-300 text-indigo-600 focus:ring-indigo-500"
                                />
                                <label
                                  htmlFor={`filter-mobile-${section.id}-${optionIdx}`}
                                  className="ml-3 min-w-0 flex-1 text-gray-500"
                                >
                                  {option.label}
                                </label>
                              </div>
                            ))}
                          </div>
                        </Disclosure.Panel>
                      </>
                    )}
                  </Disclosure>
                ))}
              </form>
            </Dialog.Panel>
          </Transition.Child>
        </div>
      </Dialog>
      </Transition.Root>
    )
}

function DesktopFilter({handleFilter,filters})
{
  return(
    <div className="hidden lg:block p-3 border-r-2 border-gray-200 min-w-[250px]">
              <h3 className="text-2xl text-black  font-semibold text-center">Filters</h3>
                <form>
                  {filters.map((section) => (
                    <Disclosure
                      as="div"
                      key={section.id}
                      className="border-b border-gray-400 py-6"
                    >
                      {({ open }) => (
                        <>
                          <h3 className="-my-3 flow-root">
                            <Disclosure.Button className="flex w-full items-center justify-between py-3 text-sm text-gray-400 hover:text-gray-500">
                              <span className="font-medium text-gray-900">
                                {section.name}
                              </span>
                              <span className="ml-6 flex items-center">
                                {open ? (
                                  <MinusIcon
                                    className="h-5 w-5"
                                    aria-hidden="true"
                                  />
                                ) : (
                                  <PlusIcon
                                    className="h-5 w-5"
                                    aria-hidden="true"
                                  />
                                )}
                              </span>
                            </Disclosure.Button>
                          </h3>
                          <Disclosure.Panel className="pt-6">
                            <div className="space-y-4">
                              {section.options.map((option, optionIdx) => (
                                <div
                                  key={option.value}
                                  className="flex items-center"
                                >
                                  <input
                                    id={`filter-${section.id}-${optionIdx}`}
                                    name={`${section.id}[]`}
                                    defaultValue={option.value}
                                    type="checkbox"
                                    defaultChecked={option.checked}
                                    onChange={(e) =>handleFilter(e, section, option)}
                                    className="h-4 w-4 rounded border-gray-300 text-indigo-600 focus:ring-indigo-500"
                                  />
                                  <label
                                    htmlFor={`filter-${section.id}-${optionIdx}`}
                                    className="ml-3 text-sm text-gray-600"
                                  >
                                    {option.label}
                                  </label>
                                </div>
                              ))}
                            </div>
                          </Disclosure.Panel>
                        </>
                      )}
                    </Disclosure>
                  ))}
                </form>
    </div>
  )
}

function ProductsFlex({handleSort,setMobileFiltersOpen})
{
  return(
    <div className="flex items-baseline  justify-end pt-2">

      <div className="flex items-center pr-[10px] sm:pr-[20px] lg:pr-[40px]">

        <Menu as="div" className="relative inline-block text-left ml-2 mr-2">
          <div>
            <Menu.Button className="group inline-flex justify-center text-xl font-medium text-gray-700 hover:text-gray-900 ">
              Sort
              <ChevronDownIcon
                className="-mr-1 mt-2 ml-1 h-5 w-5 text-xl flex-shrink-0 text-gray-400 group-hover:text-gray-500"
                aria-hidden="true"
              />
            </Menu.Button>
          </div>

          <Transition
            as={Fragment}
            enter="transition ease-out duration-100"
            enterFrom="transform opacity-0 scale-95"
            enterTo="transform opacity-100 scale-100"
            leave="transition ease-in duration-75"
            leaveFrom="transform opacity-100 scale-100"
            leaveTo="transform opacity-0 scale-95"
          >
            <Menu.Items className="absolute right-0 z-10 mt-2 w-40 origin-top-right rounded-md bg-white shadow-2xl ring-1 ring-black ring-opacity-5 focus:outline-none">
              <div className="py-1">
                {sortOptions.map((option) => (
                  <Menu.Item key={option.name}>
                    {({ active }) => (
                      <p
                        onClick={e=>handleSort(e,option)}
                        className={classNames(
                          option.current
                            ? 'font-medium text-gray-900'
                            : 'text-gray-500',
                          active ? 'bg-gray-100' : '',
                          'block px-4 py-2 text-sm'
                        )}
                      >
                        {option.name}
                      </p>
                    )}
                  </Menu.Item>
                ))}
              </div>
            </Menu.Items>
          </Transition>
        </Menu>


        <button type="button" className=" ml-2 mr-2 p-2 text-gray-500 hover:text-gray-500  lg:hidden" onClick={() => setMobileFiltersOpen(true)}>
          <span className="sr-only">Filters</span>
          <FunnelIcon className="h-6 w-6" aria-hidden="true" />
        </button>
      </div>
    </div>
  )
}

function ProductGrid({products})
{
  return(
    <div className="p-2 lg:p-4 flex flex-wrap gap-1 lg:gap-4 align-center justify-center">

        {products.map((product) => (<Product product={product}></Product>))}
    </div>
  )
}

function Product({product})
{

  return(
    (product.deleted===false || product.deleted===undefined)
    &&
    <div className='w-[150px] lg:w-[220px] bg-white'>
      <Link to={`/product-detail/${product.id}`} key={product.id} >
        <div className='flex flex-col justify-between p-1 max-h-[340px]  lg:max-h-[440px]'>
          <div className='flex justify-center h-[190px] w-[140px] lg:h-[280px] lg:w-[210px] bg-white'>
            <img
              src={product.thumbnail}
              alt={product.title}
              className="h-[100%] lg:max-w-[210px] max-w-[147px] "
            />
          </div>
          <div className="flex justify-between w-[140px] lg:w-[210px]">
            <div className=' w-[90px] lg:w-[165px]'>
              <h3 className="text-md text-black font-medium truncate">
                  {product.title}
              </h3>
            </div>
            <div className='flex flex-col justify-around w-[55px] pr-1 lg:w-[60px] text-end'>
              <p className="text-md text-black font-medium truncate text-green-600">
                $
                {product.discountPrice}
              </p>
              <p className="text-sm block line-through font-medium text-gray-400">
                ${product.price}
              </p>
            </div>
          </div>
        </div>
      </Link>
    </div>
  )
}