import { Fragment } from 'react';
import { Disclosure, Menu, Transition } from '@headlessui/react';
import {
  Bars3Icon,
  XMarkIcon,
  ShoppingCartIcon
} from '@heroicons/react/24/solid';
import { Link, Navigate } from 'react-router-dom';
import { useSelector } from 'react-redux';
import {selectLoggedInUser} from '../auth/authSlice';
import { selectItems } from '../cart/cartSlice';
import { selectUserInfo } from '../user/userSlice';


const navigation = [
  { name: 'Home', link: '/', user: true},
  { name: 'Products', link: '/products', user: true },
  { name: 'About', link: '/about', user: true },
  { name: 'Contact', link: '/contact', user: true },
];

const adminNavigation =[
  { name: 'Admin Panel', link: '/admin',admin: true},
  { name: 'Add Product', link: '/admin/product-form',admin: true},
  { name: 'Orders', link: '/admin/orders',admin: true},
]
const userNavigation = [
  { name: 'Your Profile', link: '/profile' },
  { name: 'My Orders', link:'/my-orders' },
  { name: 'Sign out', link: '/logout' },
];

function classNames(...classes) {
  return classes.filter(Boolean).join(' ');
}

function NavBar({ children }) {

  const userInfo = useSelector(selectUserInfo);
  const items = useSelector(selectItems);
  const cart = useSelector(selectItems);
  return (
    <>
      {<div className="">
        <Disclosure as="nav" className="bg-white mb-[3px] shadow-sm">
          {({ open }) => (
            <>
              <div className="px-4 sm:px-6 lg:px-8">
                <div className="flex h-16 items-center justify-between">
                  <div className="flex items-center">
                    <div className="flex-shrink-0">
                      <Link to="/" >
                      <img
                        className="h-10 w-10"
                        src="/ecommerce.png"
                        alt="Your Company"
                      />
                      </Link>
                    </div>
                    <div className="hidden md:block">
                      <div className="ml-10 flex items-baseline space-x-4">
                        {navigation.map((item) => (
                          <Link
                          to = {item.link}
                          key={item.name}
                          className= 'rounded-md px-1 px-4 py-2 text-md text-lg  hover:text-gray-400'
                          aria-current={item.current ? 'page' : undefined}
                          >
                              {item.name}
                          </Link>
                          )
                        )
                        }
                        {userInfo && userInfo.role=='admin' &&
                          adminNavigation.map((item) => (
                            <Link
                            to = {item.link}
                            key={item.name}
                            className= 'rounded-md px-1 px-4 py-2 text-md text-lg  hover:text-gray-400'
                            aria-current={item.current ? 'page' : undefined}
                            >
                                {item.name}
                            </Link>
                            )
                          )
                        }

                      </div>
                    </div>
                  </div>
                  <div className='flex'>
                  {
                    (userInfo && cart) ? (<div className="flex flex-row ">
                      <div className='flex'>
                        <Link to="/cart">
                          <button
                            type="button"
                            className="mr-7 flex-shrink-0 rounded-full bg-white p-1 text-gray-500 hover:text-gray-300 "
                            >
                            <ShoppingCartIcon
                              className="h-8 w-8 mt-1 ml-[3px]"
                              aria-hidden="true"
                              />
                          </button>
                        </Link>
                        {
                          cart.length!=0 &&
                          <span className="inline-flex absolute ml-6 mb-6 items-center rounded-md bg-red-50 px-[6px] py-[2px] text-xs font-medium text-red-700 ring-1 ring-inset ring-red-400">
                          {cart.length}
                          </span>
                        }
                      </div>
                        {/* Profile dropdown */}
                      <div>
                        <Menu as="div" className="relative">
                            <Menu.Button className="flex  items-center rounded-full  text-sm ">
                              <img
                                className="h-9 w-9 mt-1 bg-white rounded-full"
                                src='/ProfileIcon.png'
                                alt=""
                              />
                            </Menu.Button>
                          <Transition
                            as={Fragment}
                            enter="transition ease-out duration-100"
                            enterFrom="transform opacity-0 scale-95"
                            enterTo="transform opacity-100 scale-100"
                            leave="transition ease-in duration-75"
                            leaveFrom="transform opacity-100 scale-100"
                            leaveTo="transform opacity-0 scale-95"
                          >
                            <Menu.Items className="absolute right-0 z-10 mt-2 w-48 origin-top-right rounded-md bg-white py-1 shadow-lg ring-1 ring-black ring-opacity-5 focus:outline-none">
                              {userNavigation.map((item) => (
                                <Menu.Item key={item.name}>
                                  {({ active }) => (
                                    <Link
                                      to = {item.link}
                                      className={classNames(
                                        active ? 'bg-gray-100' : '',
                                        'block px-4 py-2 text-sm text-gray-700'
                                      )}
                                    >
                                      {item.name}
                                    </Link>
                                  )}
                                </Menu.Item>
                              ))}
                            </Menu.Items>
                          </Transition>
                        </Menu>
                      </div>
                    </div>) :
                      <Link
                        className='bg-[#08a3f2] text-white font-semibold py-2 px-6 rounded-lg'
                        to={'/login'}
                        >Login
                      </Link>
                  }
                  <div className="-mr-2 flex md:hidden">
                    {/* Mobile menu button */}
                    <Disclosure.Button className="inline-flex items-center  rounded-md bg-white p-2 text-gray-600">
                      {open ? (
                        <XMarkIcon
                          className="block h-8 w-8 ml-2"
                          aria-hidden="true"
                        />
                      ) : (
                        <Bars3Icon
                          className="block h-8 w-8 ml-2"
                          aria-hidden="true"
                        />
                      )}
                    </Disclosure.Button>
                  </div>
                  </div>
                </div>
              </div>

              <Disclosure.Panel className="md:hidden">
                <div className="flex flex-col text-xl pt-2 px-3">
                  {navigation.map((item) => (
                    <Link key={item.name}
                    to = {item.link}
                    className= 'rounded-md px-1 py-2 text-md font-medium hover:bg-gray-100'
                    aria-current={item.current ? 'page' : undefined}
                    >
                    {item.name}
                    </Link>
                    ))
                  }
                  {userInfo && userInfo.role=='admin' &&
                    adminNavigation.map((item) => (
                      <Link
                      to = {item.link}
                      key={item.name}
                      className= 'rounded-md px-1 px-4 py-2 text-md text-lg  hover:text-gray-400'
                      aria-current={item.current ? 'page' : undefined}
                      >
                          {item.name}
                      </Link>
                      )
                    )
                  }
                </div>
              </Disclosure.Panel>
            </>
          )}
        </Disclosure>

        <main>
          <div className="mx-auto max-w-full">
            {children}
          </div>
        </main>
      </div>
      }
    </>
  );
}

export default NavBar;