
import { Link } from 'react-router-dom'
import React from 'react'

import LinkedInIcon from '@mui/icons-material/LinkedIn';
import EmailIcon from '@mui/icons-material/Email'
import GitHubIcon  from '@mui/icons-material/GitHub';
export const FooterSection = () =>
{
   return <footer class="flex flex-col justify-center p-4  bg-gray-500">
   <div class="flex justify-center flex-wrap gap-6 text-white font-medium">
      <Link to = {'/'} key='home' class="hover:text-gray-900">
        Home
      </Link>
      <Link to = {'/products'} key='products' class="hover:text-gray-900">
      Products
      </Link>
      <Link to = {'/about'} key='about' class="hover:text-gray-900" >
        About
      </Link>
      <Link to = {'/contact'} key='contact' class="hover:text-gray-900" >
        Contact
      </Link>
   </div>
   <div class="flex justify-center space-x-5">

       <a href="https://www.linkedin.com/in/sumit-tupe-8027a0241/" target="_blank" rel="noopener noreferrer">
        <LinkedInIcon className='h-[10px] w-[10px] text-white'></LinkedInIcon>
       </a>
       <a href="https://github.com/Sumit9623" target="_blank" rel="noopener noreferrer">
        <GitHubIcon className='h-[10px] w-[10px] text-white'></GitHubIcon>
       </a>
       <a href="mailto:sumittupe813@gmail.com" target="_blank" rel="noopener noreferrer">
        <EmailIcon className='h-[10px] w-[10px] text-white'></EmailIcon>
       </a>
   </div>
   <p class="text-center text-white font-medium">FashCart</p>
</footer>
}
export default FooterSection;