import { useState, useEffect } from 'react';
import { useDispatch, useSelector } from 'react-redux';
import { fetchProductByIdAsync, selectProductById,selectProductListStatus } from '../productSlice';
import { Link, Navigate, useParams } from 'react-router-dom';
import { addToCartAsync, selectItems } from '../../cart/cartSlice';
import Protected from '../../auth/components/Protected';
import { selectLoggedInUser } from '../../auth/authSlice';


// TODO: In server data we will add colors, sizes , highlights. to each product


function classNames(...classes) {
  return classes.filter(Boolean).join(' ');
}

// TODO : Loading UI

export default function ProductDetail() {
  const items = useSelector(selectItems);
  const product = useSelector(selectProductById);
  const dispatch = useDispatch();
  const userInfo = useSelector(selectLoggedInUser);
  const params = useParams();

  const [selectedSize, setSelectedSize] = useState();
  const [count, setcount] = useState(1);
  const [activeImg, setActiveImage] = useState()

  useEffect(() => {
    dispatch(fetchProductByIdAsync(params.id));
  }, [dispatch, params.id]);

  useEffect(()=>{
    if(product!==null) setActiveImage(product.images[0])
  },[product])

  const handleCart = (e) => {
    e.preventDefault();
    if(selectedSize)
    {
      if (items.findIndex((item) => item.product.id === product.id) < 0) {
        const newItem = {
          product: product.id,
          quantity: count,

        };
        if (selectedSize) {
          newItem.size = selectedSize;
        }
        dispatch(addToCartAsync(newItem));
        alert('Item added to Cart');
      }
      else {
        alert('Item Already added');
      }
    }
    else{
      alert("Select Size");
    }
  };



  return (
    <div className="bg-white">

    {product &&
        <div className='flex flex-col justify-between p-3 lg:p-7 lg:flex-row gap-16 '>
          <div className='flex flex-col lg:flex-row  gap-6 justify-center align-center'>
              <div  className='flex justify-center w-[95%] lg:w-[40vw] lg:max-h-[70vh] shadow-lg  rounded-xl'>
                  <img src={activeImg} alt="" className='h-[100%]'/>
              </div>
              <div className='flex flex-row lg:flex-col gap-2 lg:order-first  justify-between'>
                  {product.images.map((item)=>{
                      return <img src={item} alt="" className='w-[23%] lg:w-[80%] min-h-[100px] max-h-[150px] shadow-lg rounded-md cursor-pointer' onClick={() => setActiveImage(item)}/>

                  })}
              </div>
          </div>
          {/* ABOUT */}
          <div className='flex flex-col lg:w-2/4'>
              <div>
                  <h1 className='text-5xl font-md text-[#08a3f'>{product.title}</h1>
                  <h1 className='text-xl ml-3 font-md text-black'>{product.brand}</h1>
              </div>
              <h6 className='text-2xl font-semibold  ml-3 mt-2 text-green-600'>${product.discountPrice}</h6>
              <div className='flex wrap'>
                  <div className='w-[60px]'> <h6 className='text-lg  font-md  ml-3 line-through text-red-400'>${product.price}</h6> </div>
                  <div className='w-[100px]  rounded-md'><h6 className='text-md text-green-600 font-md  ml-3 '>{product.discountPercentage}% Off</h6> </div>
              </div>
              <p className='text-gray-700  ml-3'>
                {product.description}
              </p>
              {product.highlights && product.highlights.length>0 && <div>
                  <h6 className='text-md font-semibold ml-3 mt-2'>Highlightes:</h6>
                  <ul className='list-disc ml-8'>
                    {
                      product.highlights.map((item)=>{
                        return (
                          <li>{item}</li>

                        )
                      })
                    }
                  </ul>
              </div>}
              {product.sizes && product.sizes.length>0 && <div>
                  <h6 className='text-md font-semibold ml-3 mt-2'>Available Sizes</h6>
                  <div className='flex flex-row flex-wrap gap-2 mt-1 ml-3'>
                      {product.sizes.map((item)=>{
                          return (
                              item.stock>0 ?
                              <div
                              className= {
                                    (item.size===selectedSize)
                                    ? 'bg-green-600 text-lg px-2 py-2 text-white rounded-md min-w-[60px] text-center'
                                    : 'bg-[#08a3f2] text-lg px-2 py-2 text-white rounded-md min-w-[60px] text-center'
                                  }
                              onClick={(e)=>{
                                setSelectedSize(item.size);
                                }}
                              >{item.size}</div>
                              :
                              <div className='bg-gray-200 text-lg px-2 py-2 text-gray-600 rounded-md min-w-[60px] text-center line-through'>{item.size}</div>
                              )
                          })}
                  </div>
              </div>}
              <div className='flex flex-row items-center gap-6 space-between  ml-3 my-2'>
                  <div className='flex flex-row items-center'>
                      <button className='bg-[#08a3f2] py-1 px-4 rounded-lg text-white text-2xl' onClick={() => setcount((prev) => prev - 1)}>-</button>
                      <span className='py-4 px-6 rounded-lg'>{count}</span>
                      <button className='bg-[#08a3f2] py-1 px-4 rounded-lg text-white text-2xl' onClick={() => setcount((prev) => prev + 1)}>+</button>
                  </div>
                    {
                     userInfo ? <button
                        className='bg-[#08a3f2] text-white font-semibold py-2 px-6 rounded-lg'
                        onClick={handleCart}
                        >Add to Cart
                      </button> :
                      <Link
                        className='bg-[#08a3f2] text-white font-semibold py-2 px-6 rounded-lg'
                        to={'/login'}
                        >Sign in to continue
                      </Link>
                    }
              </div>
          </div>
      </div>
    }
    </div>
  );
}

