import React, {useRef} from 'react'
import emailjs from '@emailjs/browser';
import NavBar from '../features/navbar/Navbar';
import Footer from '../features/common/Footer';
const ContactUsPage=()=>{
    return(
        <div>
            <NavBar>
                <ContactUs></ContactUs>
            </NavBar>
            <Footer></Footer>
        </div>
    )
}

const ContactUs = () =>
{
    const form = useRef();
    const sendEmail = (e) => {
        e.preventDefault();
        emailjs.sendForm(process.env.REACT_APP_SERVICE_ID,process.env.REACT_APP_TEMPLATE_ID, form.current,process.env.REACT_APP_EMAILJS_API)
          .then((result) => {
              alert("Message Sent");
          }, (error) => {
              alert("Error while sending");
          });
      };

   return <div className='max-w-6xl m-auto bg-white p-4'>
    <h3 className="mt-6 text-4xl font-semibold text-[#08a3f2] text-center"> Contact Us</h3>
    <p class="mb-3 mt-4 text-2xl text-center  text-gray-600">
        If you have any Issue Please feel free to ask
    </p>

   <div class="flex items-center justify-center p-7">

     <div class="mx-auto w-full max-w-[550px]">
       <form ref={form} onSubmit={sendEmail}>
         <div class="mb-5">
           <label
             for="name"
             class="mb-3 block text-base font-medium text-[#07074D]"
           >
             Full Name
           </label>
           <input
             type="text"
             name="name"
             id="name"
             placeholder="Full Name"
             class="w-full rounded-md border border-[#e0e0e0] bg-white py-3 px-6 text-base font-medium text-[#6B7280] outline-none focus:border-[#6A64F1] focus:shadow-md"
             />
         </div>
         <div class="mb-5">
           <label
             for="email"
             class="mb-3 block text-base font-medium text-[#07074D]"
             >
             Email Address
           </label>
           <input
             type="email"
             name="email"
             id="email"
             placeholder="example@domain.com"
             class="w-full rounded-md border border-[#e0e0e0] bg-white py-3 px-6 text-base font-medium text-[#6B7280] outline-none focus:border-[#6A64F1] focus:shadow-md"
             />
         </div>
         <div class="mb-5">
           <label
             for="subject"
             class="mb-3 block text-base font-medium text-[#07074D]"
             >
             Subject
           </label>
           <input
             type="text"
             name="subject"
             id="subject"
             placeholder="Enter your subject"
             class="w-full rounded-md border border-[#e0e0e0] bg-white py-3 px-6 text-base font-medium text-[#6B7280] outline-none focus:border-[#6A64F1] focus:shadow-md"
             />
         </div>
         <div class="mb-5">
           <label
             for="message"
             class="mb-3 block text-base font-medium text-[#07074D]"
             >
             Message
           </label>
           <textarea
             rows="4"
             name="message"
             id="message"
             placeholder="Type your message"
             class="w-full resize-none rounded-md border border-[#e0e0e0] bg-white py-3 px-6 text-base font-medium text-[#6B7280] outline-none focus:border-[#6A64F1] focus:shadow-md"
             ></textarea>
         </div>
         <div>
           <button
             class="hover:shadow-form rounded-md bg-[#08a3f2] py-3 px-8 font-semibold text-white"
             type='submit'
             >
             Send
           </button>
         </div>
       </form>
     </div>
   </div>
</div>
}

export default ContactUsPage;